import React from 'react';
import moment from 'moment';
import StatCard from './StakingStatCard';
import { Grid, Typography } from '@material-ui/core';
import useStakingData from '../../hooks/useStakingData';

const StakingStats = () => {
    const { stakingStats, stakingStatsLoading } = useStakingData();

    return (
        <div style={{ padding: '2%', paddingBottom: 0 }}>
            <Typography variant="h4" align="center" color="primary" style={{ fontWeight: '100' }}>Staking Stats</Typography>
            <Typography variant="subtitle2" color="textSecondary" style={{ fontWeight: '400' }} align="center">Data Last Refreshed: {moment(stakingStats?.timestamp).format("h:mm a")}</Typography>
            <Typography variant="subtitle2" color="textSecondary" style={{ fontWeight: '400' }} align="center">Data as of Block: {stakingStats?.block}</Typography>
            
            <div style={{ margin: '2%' }}>
                <Grid container spacing={3} justify="center">
                    <Grid item lg={3} md={6} xs={10}>
                        <StatCard stat="Total Active Stakes" amount={`${stakingStats?.current_stakers?.toLocaleString()} (${Math.round(stakingStats?.current_axn_staked)?.toLocaleString()} AXN)`} suffix="" loading={stakingStatsLoading} />
                    </Grid>
                    <Grid item lg={3} md={6} xs={10}>
                        <StatCard stat="Total 5,555 Club Members" amount={`${stakingStats?.current_5555_club_members?.toLocaleString()} (${Math.round(stakingStats?.current_5555_club_staked_axn)?.toLocaleString()} AXN)`} suffix="" loading={stakingStatsLoading} />
                    </Grid>
                    <Grid item lg={3} md={6} xs={10}>
                        <StatCard stat="Average Stake Length" amount={Math.round(stakingStats?.average_stake_length)?.toLocaleString()} suffix="Days" loading={stakingStatsLoading} />
                    </Grid>
                    <Grid item lg={3} md={6} xs={10}>
                        <StatCard stat="Average Stake Amount" amount={Math.round(stakingStats?.average_stake_amount)?.toLocaleString()} suffix="AXN" loading={stakingStatsLoading} />
                    </Grid>   
                </Grid>
            </div>
        </div>
    )
}

export default StakingStats;